import { CSSProperties, ReactNode } from "react";

import { PopoverContentProps, PopoverProps } from "@radix-ui/react-popover";

import { AvatarProps } from "../Avatar";
import { DropDownProps } from "../DropDown/DropDown.types";

export interface FilterProps {
	filterName: string;
	filterId: string;
	filterComponent?: React.ReactNode;
	filterAvatar?: React.ReactElement<AvatarProps>;
}

export interface GroupFilterProps {
	label: string;
	groupFilters: FilterProps[]
}

export interface FilterItemProps {
	focus?: boolean;
	setFocus: (index: number) => void;
	index?: number;
	setSelectedFilter: (filter: FilterProps) => void;
	filter: FilterProps;
	onSelectFilterFunc?: (filterName?: string, filterId?: string) => void;
	closeMenu?: () => void;
	$isSelected?: boolean;
	dataQA: string;
}

export interface FilterMenuStyleProps {
	$menuHeight?: string;
	$menuWidth?: string;
	$zIndex?: CSSProperties["zIndex"];
	$isSelected?: boolean;
	radixPopoverContentProps?: PopoverContentProps;
}

export interface FilterMenuProps extends DropDownProps, FilterMenuStyleProps {
	dataQA: string;
	itemSelected?: string;
	filters: FilterProps[] | GroupFilterProps[];
	/* eslint-disable @typescript-eslint/no-explicit-any */
	customSearchFunc?: (searchValue: string, filters: any[]) => FilterProps[] | GroupFilterProps[];
	addButtonFunc?: (filterName?: string, filterId?: string, filterComponent?: React.ReactNode) => void;
	addButtonText?: string;
	onSelectFilterFunc?: (filterName?: string, filterId?: string) => void;
	topActions?: ReactNode;
	radixPopoverProps?: PopoverProps;
}

export interface FilterPageProps extends Omit<FilterMenuProps, "filters"> {
	filterId: string;
	filterName: string;
	closeButtonFunc: React.MouseEventHandler<HTMLButtonElement>;
	filterComponent?: React.ReactNode;
}
